/*##############################
  Ridematch base styles
##############################*/
.ridematch {
    color: $font-color;
    font-family: $font-stack-lato;

    h1 {
        font-size: 16px;
    }

    h2 {
        font-size: 16px;
    }

    h3 {
        font-size: 10px;
    }

    p {
        font-size: 10px;
    }

    a {
        font-size: 10px;

        &:hover {
            text-decoration: none;
        }
    }

    .btn {
        font-size: 13px;
    }

    hr {
        color: $light-grey;
        background-color: $light-grey;
    }

    small {
        font-size: 8px;
    }

    .card {
        background-color: $white;
        border: 1px solid $grey;
        padding: 15px;
    }

    .navigation {
        background-color: $white;

        i {
            padding: 15px;
        }
    }

    .btn {
        text-align: center;
    }

    .red {
        color: $red-text;
    }

    .margin-bottom {
        margin-bottom: 10px;
    }

    .leadin {
        font-size: 14px;
        font-weight: 300;
        line-height: 1.3;
    }

    .material-icons {
        font-size: 16px;
        width: 16px;
        height: 16px;
        position: relative;
        top: 2px;
    }

    a i.material-icons {
        color: $font-color;
        font-size: 10px;
        width: 10px;
        height: 10px;
    }

    a.active i.material-icons {
        color: $transit-blue;
    }
    // Media queries
    @include screen ('desktop') {
        h1 {
            font-size: 30px;
        }

        h2 {
            font-size: 24px;
        }

        a {
            font-size: 12px;
        }

        small {
            font-size: 8px;
        }

        .btn {
            text-align: left;
        }

        .material-icons {
            font-size: 20px;
            width: 20px;
            height: 20;
        }

        .leadin {
            font-size: 24px;
        }
    }
    @include screen('lg-desktop') {
        h2 .material-icons {
            top: 3px;
        }
    }
}
